@mixin flex ($direction , $justify , $align){
    display: flex;
    flex-direction: $direction;
    justify-content: $justify;
    align-items: $align;
}
@mixin wh($width , $height){
    width: $width;
    height: $height;
}
@mixin mq($size){
    @media (max-width: $size){
        @content
    }
}